*,*::before,*::after{box-sizing:border-box;color:inherit}html,body{overflow:hidden}body{color:#2d4c53;font-family:"Arimo","Helvetica",sans-serif;line-height:1.5;margin:0;padding:0}.content-section{background:#024a58;height:calc(100vh - 54px);overflow:auto}@media(max-width: 486px){.content-section{height:calc(100vh - 72px)}}.container{margin:0 auto;max-width:720px;overflow:hidden;padding:88px 0}@media(max-width: 800px){.container{padding:42px 0}}@media(max-width: 486px){.container{padding:18px 0}}a{color:#e77f1f;text-decoration:none}.invisible-but-outline-readable{height:0;margin:0;overflow:hidden;padding:0;width:0}h1,h2,h3,h4,h5,h6{font-family:"Arimo","Helvetica",sans-serif;text-align:center}h2{color:#eef5f7;font-size:40px;margin:0 0 40px}@media(max-width: 800px){h2{font-size:38px;margin-bottom:20px}}@media(max-width: 600px){h2{font-size:36px;margin-bottom:15px}}@media(max-width: 486px){h2{font-size:33px;margin-bottom:12px}}@media(max-width: 325px){h2{font-size:30px;margin-bottom:10px}}h3{color:#1c7889;margin-bottom:0;margin-top:1.5em}